import React from 'react';

import './App.css';

import { View, Text, Button, Divider } from 'core';

function Image({ src, selected }: { src?: string, selected?: boolean; }) {
  return (
    <View style={{ position: 'relative', borderRadius: 3 }}>
      {selected && (
        <View style={{ position: 'absolute', inset: -4, border: '3px solid #339af0', borderRadius: 6 }} />
      )}
      <img src={src} style={{ width: '100%', height: 'auto', borderRadius: 2.5, overflow: 'hidden' }} />
    </View>
  );
}

